/* eslint-disable no-use-before-define */
import { compare } from "./compare.js"
import { comparisonToErrorMessage } from "./toErrorMessage/comparisonToErrorMessage.js"

export const assert = ({ message, actual, expected }) => {
  const expectation = {
    actual,
    expected,
  }

  const comparison = compare(expectation)
  if (comparison.failed) {
    if (message) {
      throw createAssertionError(message)
    } else {
      throw createAssertionError(comparisonToErrorMessage(comparison))
    }
  }
}

export const isAssertionError = (value) =>
  value && typeof value === "object" && value.name === "AssertionError"

const createAssertionError = (message) => {
  const error = new Error(message)
  error.name = "AssertionError"
  return error
}
