/* eslint-disable no-use-before-define */
import { uneval } from "@dmail/uneval"
import { comparisonToSubject } from "../comparisonToSubject.js"

export const symbolsComparisonToErrorMessage = (comparison) => {
  if (comparison.type !== "symbols") return undefined

  const subject = comparisonToSubject(comparison, "actual")

  return `unexpected symbols at ${subject}
--- missing symbols ---
${uneval(comparison.actual.missing)}
--- extra symbols ---
${uneval(comparison.actual.extra)}`
}
